.breadcrumbs {
    .oswald();
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        .clearfix();
    }
    &__item {
        float: left;
        padding-right: 15px;
        position: relative;
        &:before {
            content: '';
            background: url('../assets/icons/small-chevron-right.svg') no-repeat;
            display: inline-block;
            width: 7px;
            height: 8px;
            position: absolute;
            right: 3px;
            top: 8px;
        }
        &:last-child {
            &:before {
                display: none;
            }
        }
    }
    &__link {
        font-size: 16px;
        letter-spacing: 0.3px;
        text-decoration: none;
        text-transform: uppercase;
        color: @colorMainBlue;
        transition: color .3s;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
}
