@import "mixins";

@iconEmptySize: 30px;

.avatar-image {
    position: relative;
    width: @accountAvatarWidth;
    height: @accountAvatarWidth;
    background-color: white;
    border-radius: 50%;
    box-shadow: 0 4px 4.5px 0 rgba(0, 0, 0, 0.21);
    &__icon-empty {
        .abs-center();
        width: @iconEmptySize;
        height: @iconEmptySize;
    }
    &__img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-size: cover;
        background-position: 50% 50%;
        background-repeat: no-repeat;
    }
}
