.incrementer {
    text-align: center;
    width: 30px;
    display: inline-block;
    &__input {
        display: inline-block;
        width: 30px;
        height: 30px;
        background: #fff;
        border: solid 2px @colorBlack;
        color: @colorBlack;
        font-size: 16px;
        text-align: center;
        box-sizing: border-box;
        .oswald();
        &:focus {
            outline: none;
        }
    }
    &__btn {
        cursor: pointer;
        color: @colorMain;
        display: inline-block;
        width: 20px;
        height: 20px;
        &:hover {
            color: @colorMainHover;
        }
        &:active {
            color: @colorMainActive;
        }
        &_plus {
            margin-bottom: 10px;
        }
        &_minus {
            margin-top: 4px;
        }
    }
    .icon {
        fill: currentColor;
    }
}
