.dispensary-item {
    color: @colorBlack;
    padding: 13px 26px;
    box-sizing: border-box;
    cursor: pointer;
    transition: background .3s;
    .clearfix();
    &:hover {
        background: @lightGrayColor;
    }
    &:active {
        transition: none;
        background: darken(@lightGrayColor, 10%);
    }
    &__header {
        .clearfix();
    }
    &__status {
        float: left;
        background: #61bf61;
        font-size: 14px;
        color: #fff;
        height: 20px;
        line-height: 20px;
        padding: 0 10px;
        &_inactive {
            background: @colorMainDisabled;
        }
    }
    &__features {
        float: left;
        list-style: none;
        padding: 0;
        margin: 0;
        margin-left: 7px;
    }
    &__features-item {
        float: left;
        margin-right: 10px;
        padding-top: 2px;
        &:last-child {
            margin-right: 0;
        }
        .icon {
            float: left;
            margin-right: 5px;
        }
        .icon-pick-up {
            stroke: currentColor;
            fill: none;
        }
        .icon-delivery {
            fill: currentColor;
            width: 16px;
            height: 14px;
            margin-top: 2px;
        }
    }
    &__features-text {
        font-size: 14px;
        margin: 0;
        float: left;
    }
    &__container {
        float: left;
        width: 93%;
    }
    &__title {
        .oswald();
        margin: 0;
        font-size: 20px;
        text-transform: uppercase;
        letter-spacing: -0.02em;
    }
    &__place {
        font-size: 14px;
        line-height: 1;
    }
    &__distance {
        font-size: 11px;
        color: @warmGrayColor;
        margin-bottom: 2px;
    }
    &__price-container {
        font-size: 14px;
        float: right;
        width: 7%;
        text-align: center;
        padding-top: 3px;
    }
    &__weight {
        line-height: 1;
    }
    &__price {
        font-weight: @bold;
    }
    &_active {
        background: @lightGrayColor !important;
        cursor: default;
    }
}
