.photo-modal {
    position: fixed;
    background: rgba(0,0,0,.6);
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    &__inner {
        position: absolute;
        top: 50%;
        left: 50%;
        padding: 0 70px;
        transform: translate(-50%, -50%);
    }
    &__arrow {
        width: 36.3px;
        height: 70px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        transition: opacity .3s;
        .icon {
            stroke: #fff;
        }
        &:hover {
            opacity: .7;
        }
        &:active {
            transition: none;
            opacity: 1;
        }
        &_prev {
            left: 10px;
            transform: translateY(-50%);
        }
        &_next {
            right: 10px;
        }
    }
    &__photo {
        max-width: 600px;
        max-height: 300px;
        .no-select();
    }
}
