@import "vars";
@import "mixins";

.input {
    font-size: 16px;
    line-height: 24px;
    width: 100%;
    height: 72px;
    display: inline-block;
    position: relative;
    font-family: @fontMain;
    font-weight: @thin;
    background-color: transparent;
    margin-bottom: -4px;
    &_pointable {
        cursor: pointer;
    }
    &_slim {
        height: 48px;
    }
    &__label {
        position: absolute;
        line-height: 22px;
        top: 41px;
        transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        z-index: 1;
        transform: scale(0.75) translate(0px, -28px);
        transform-origin: left top 0px;
        pointer-events: none;
        color: @colorWarmGray;
        text-transform: uppercase;
        font-size: 15px;
        -webkit-user-select: none;
    }
    &__text {
        box-sizing: border-box;
        position: absolute;
        opacity: 1;
        color: @colorBlack;
        font-weight: @light;
        bottom: 10px;
        width: 100%;
        .dots();
        &:empty {
            display: inline-block;
            width: 100px;
            height: 24px;
        }
        &_empty {
            color: @colorMainDisabled;
        }
    }
    &__body {
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        padding: 0px 40px 0px 0px;
        position: relative;
        width: 100%;
        border: none;
        outline: none;
        color: @colorBlack;
        cursor: not-allowed;
        font-style: inherit;
        font-variant: inherit;
        font-weight: inherit;
        font-stretch: inherit;
        font-size: inherit;
        line-height: inherit;
        font-family: inherit;
        height: 100%;
        box-sizing: border-box;
        margin-top: 14px;
        background-color: rgba(0, 0, 0, 0);
    }
    &__hr {
        box-sizing: content-box;
        border-bottom: 2px solid @colorMainBlue;
        bottom: 8px;
        margin: 0px;
        position: absolute;
        width: 100%;
        display: block;
        cursor: not-allowed;
    }
}
