@import "mixins";

.main-page {
    &__header-bg {
        .image-2x("../assets/images/imgBG.jpg");
        height: 180px;
        background-position: 50% 23%;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .shipment__row_left {
        justify-content: center !important;
    }
}


@media (max-width: 500px) {
    .main-page {
        .product-card {
            &__info-block {
                height: auto;
                top: 33px;
            }
            &__content {
                padding-top: 31px;
            }
            &__sold-by {
                padding-bottom: 29px;
            }
        }
    }
}
