/*
    dropdown classes, because we have to use cascade to style stuff, which unavailable due inline-style
*/
.canny-dropdown > div > div:nth-child(2) {
    line-height: 28px !important;
}

.canny-dropdown > div > svg {
    top: 2px !important;
    fill: #f0266a !important;
}

.canny-dropdown > div > div:nth-child(4) {
    display: none !important;
}
