@import "vars";

.legal {
    margin: 123px auto 120px auto;
    &__header {
        font-family: @fontDecorative;
        text-align: center;
        line-height: 55px;
        font-size: 50px;
        font-weight: normal;
        text-transform: uppercase;
        color: @colorBlack;
    }
    &__content {
        margin: 98px auto 0 auto;
        max-width: 1042px;
        &-title {
            font-size: 25px;
            font-weight: normal;
            margin: 30px 0 10px 0;
            &_pink {
                color: @colorMain;
                font-size: 40px;
                margin: 40px 0 10px 0;
            }
        }
        &-paragraph {
            font-size: 20px;
            line-height: 1.5;
            letter-spacing: 0.4px;
            color: @colorBlack;
            margin: 0 0 30px 0;
            a {
               color: @colorMain;
                text-decoration: none;
            }
        }
    }
}

@media (max-width: @DESKTOP_BREAKPOINT) {
    .legal {
        margin: 110px auto 110px auto;
        &__content {
            margin: 92px auto 0 auto;
            max-width: 692px;
            padding: 0 37px;
        }
    }
}

@media (max-width: @TABLET_BREAKPOINT) {
    .legal {
        margin: 93px auto 110px auto;
        &__header {
            font-size: 28px;
            margin: 0;
        }
        &__content {
            margin: 43px auto 0 auto;
            padding: 0 22px;
            &-title {
                font-size: 25px;
            }
        }
    }
}
