@import "vars";

@avatarUploadSize: 34px;

.avatar-upload {
    width: @avatarUploadSize;
    height: @avatarUploadSize;
    position: absolute;
    background-color: white;
    border: solid 1px @colorMainDisabled;
    border-radius: 50%;
    cursor: pointer;
    &__icon {
        .abs-center();
        width: 16px;
        height: 13px;
    }
    &_account {
        position: absolute;
        bottom: -5px;
        right: -10px;
    }
}
