@import "vars";
@import "mixins";

// Desktop
@media (min-width: (@DESKTOP_BREAKPOINT + @SCROLLBAR)) {
    // .message('Desktop');
    .effects {
        &__item {
            &:nth-child(5n + 5) {
                margin-right: 0;
            }
            &:nth-last-child(-n+5) {
                margin-bottom: 0;
            }
        }
    }
    .post-effects {
        &__item {
             &:nth-child(5n + 5) {
                margin-right: 0;
            }
            &:nth-last-child(-n+5) {
                margin-bottom: 0;
            }
        }
    }
}

// Tablet
@media (max-width: (@DESKTOP_BREAKPOINT + @SCROLLBAR)) {
    // .message('Tablet');
    .product-details {
        padding: 0 89px;
        &__description {
           padding: 0 89px;
        }
    }
    .btn {
        &_print-receipt {
            display: none;
        }
    }
    .effects {
        &__item {
            &:nth-child(5n + 5) {
                margin-right: 0;
            }
            &:nth-last-child(-n+5) {
                margin-bottom: 0;
            }
        }
    }
    .post-effects {
        &__item {
             &:nth-child(5n + 5) {
                margin-right: 0;
            }
            &:nth-last-child(-n+5) {
                margin-bottom: 0;
            }
        }
    }
    .layout {
        max-width: 710px;
        &_photos {
            max-width: 740px;
        }
        &__row {
            padding: 0 10px;
        }
        &__column {
            width: 100%;
            padding: 0;
        }
        &_middle {
            margin-bottom: 50px;
            .layout__column {
                width: 50%;
                padding: 0 30px;
            }
            .layout__column-group-2-3 {
                width: 50%;
                .layout__column {
                    width: 100%;
                    margin-bottom: 50px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    .reviews {
        padding: 0 99px;
    }
    .breadcrumbs {
        display: none;
    }
    .gallery {
        display: none;
    }
    .section {
        padding-top: @headerHeight;
        &__content {
            padding: 0 60px;
        }
    }
    .shop {
        &__row {
            &_shipment {
                margin-bottom: 5px !important;
            }
        }
    }
}

// Portrait
@media (max-width: (@TABLET_BREAKPOINT + @SCROLLBAR)) {
    // .message('Portrait');
    .btn {
        &_print-receipt {
            display: none;
        }
    }
    .pager {
        width: 100%;
        &__content {
            width: 100%;
            justify-content: space-between;
        }
        &__item {
            margin-right: 0;
        }
    }
    .section {
        padding-top: 0;
    }
    .section-header {
        &__title {
            margin-bottom: 0;
        }
    }
    .shop {
        &__row {
            &_shipment {
                margin-bottom: 5px !important;
            }
        }
    }
    .photos {
        max-width: 500px;
    }
}

// Mobile
@media (max-width: (@PORTRAIT_BREAKPOINT + @SCROLLBAR)) {
    .mobile-show {
        display: block;
    }
    .mobile-hide {
        display: none;
    }
    // .message('Mobile');
    .rating-detailed {
        &__info {
            width: 100%;
        }
    }
    .order-details {
        margin-bottom: 50px;
    }
    .dispansary-short {
        text-align: center;
    }
    .photos {
        display: none;
        &__disclaimer {
            display: block;
        }
    }
    .layout {
        max-width: 320px;
        min-width: 300px;
        &__row {
            padding: 0 10px;
        }
        &__column {
            width: 100%;
            padding: 0 !important;
        }
        &_middle {
            margin-bottom: 50px;
            .layout__column-group-2-3 {
                width: 100%;
                .layout__column {
                    width: 100%;
                    margin-bottom: 50px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
            .layout__column_33 {
                width: 100%;
            }
        }
    }
    .effects {
        &__item {
            &:nth-child(5n + 5) {
                margin-right: 10px;
            }
            &:nth-last-child(-n+5) {
                margin-bottom: 8px;
            }
            &:nth-child(3n + 3) {
                margin-right: 0;
            }
            &:nth-last-child(-n+3) {
                margin-bottom: 0;
            }
        }
    }
    .post-effects {
        &__item {
             &:nth-child(5n + 5) {
                margin-right: 10px;
            }
            &:nth-last-child(-n+5) {
                margin-bottom: 8px;
            }
            &:nth-child(3n + 3) {
                margin-right: 0;
            }
            &:nth-last-child(-n+3) {
                margin-bottom: 0;
            }
        }
    }
    .product-details {
        padding: 0;
        &__description {
            padding: 0;
        }
    }
    .reviews {
        padding: 0 10px;
    }
    .btn {
        &_print-receipt {
            display: none;
        }
    }
    .btn-set {
        .btn {
            margin-right: 10px;
            &:last-child {
                margin-right: 0;
            }
        }
        &_margin-bottom {
            margin-bottom: 0;
        }
    }
    .main-logo {
        width: 90%;
    }

    .tablet-hidden {
        display: none;
    }

    .pager {
        width: 100%;
        &__content {
            width: 100%;
            justify-content: space-between;
        }
        &__item {
            margin-right: 0;
        }
    }

    .shop {
        &__row {
            &_shipment {
                margin-bottom: 5px !important;
            }
        }
    }

    .how-it-works__item {
        width: 512px;
    }

    .section {
        padding-top: 0;
    }

    .section-header {
        &__title {
            margin-bottom: 0;
        }
    }

    .account {
        &__section-header {
            display: block;
        }
        &__section {
            padding: 0;
            &_mobile {
                padding-bottom: 20px;
            }
            &_carousel {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
            }
        }
    }

    .section {
        &__content {
            padding: 0;
        }
    }
}
