.gallery {
    margin-top: 31px;
    .clearfix();
    &__preview {
        float: left;
        width: 100%;
        height: 358px;
        border: solid 1px @colorMainDisabled;
    }
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        margin-top: 13px;
        float: left;
    }
    &__item {
        float: left;
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        float: left;
        width: 85px;
        height: 55px;
        border: solid 1px @colorMainDisabled;
        &_active {
            border: solid 1px @colorMainBlue;
        }
    }
}
