@import "vars";

.account {
    padding-bottom: 50px;
    &__section {
        box-sizing: border-box;
        width: 100%;
        padding: 0 101px;
        &_carousel {
            width: 100%;
        }
        &_padded-top {
            padding-top: 50px;
        }
        &_padded {
            padding-bottom: 40px;
        }
        &_no-padding {
            padding-bottom: 0;
        }
        .section-header__title {
            margin: 0;
        }
    }
    &__section-header {
        .no-select();
        position: relative;
        margin: 6px 0;
        display: none;
        font-family: @fontDecorative;
        letter-spacing: -1px;
        font-weight: 500;
        text-transform: uppercase;
        cursor: pointer;
        font-size: 30px;
        text-align: center;
    }
    &__section-header-icon {
        position: absolute;
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px;
        top: 18px;
    }
    &__section-footer {
        padding-top: 20px;
        &_centred {
            text-align: center;
        }
    }
    &__footer {
        padding-bottom: 50px;
    }
}
