.layout {
    padding-top: @headerHeight;
    width: 100%;
    max-width: 1310px;
    margin: 0 auto;
    .clearfix();
    &__row {
        .clearfix();
        &_margin-top {
            margin-top: 30px;
        }
    }
    &__column-group-2-3 {
        width: 100% / 3 *2;
        float: left;
        .layout__column {
            width: 50%;
        }
    }
    &__column {
        float: left;
        box-sizing: border-box;
        &_50 {
            width: 50%;
        }
        &_33 {
            width: 100% / 3;
        }
        &_gutter {
            padding: 0 30px;
        }
    }
    &_no-padding {
        padding-top: 0;
    }
    &_middle {
        max-width: 1060px;
    }
    &_narrow {
        max-width: 512px;
    }
}
