@import "vars";

.main-logo {
    display: block;
    margin: -50px auto 0;
    max-width: 512px;
    min-width: 310px;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 7px;
    position: relative;
    text-align: center;
    background: @colorMain;
    &__title {
        display: inline-block;
        font-family: @fontDecorative;
        text-transform: uppercase;
        font-size: 52px;
        font-weight: normal;
        letter-spacing: 23.2px;
        color: @lightGrayColor;
        margin: 0;
        padding: 0;
    }
    &__slogan {
        font-family: @fontMain;
        font-weight: @normal;
        font-size: 13px;
        color: @lightGrayColor;
        margin: -10px 0 0;
        padding: 0;
    }
}
