@import "vars";

.text-field {
    position: relative;
    &_marged {
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &_btn {
        .input__text {
            padding-right: 20px;
        }
    }
    input[type="text"] {
        &[disabled] {
            color: @colorBlack !important;
        }
    }
    button {
        &[disabled] * {
            color: @colorMainDisabled !important;
        }
    }
}
