@import "vars";

.dialog {
    &_mobile {
        padding: 0 5px;
    }
    &__header {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: 0 auto;
        width: 100%;
        max-width: 416px;
        text-align: center;
        position: relative;
        z-index: 1;
        min-height: 50px;
        &-title {
            display: inline-block;
            font-family: @fontDecorative;
            margin: 0;
            text-transform: uppercase;
            letter-spacing: -1px;
            font-size: 32px;
            font-weight: 500;
            line-height: 32px;
            text-align: center;
            padding: 0 30px;
            z-index: 1;
            @media (max-width: @MOBILE_BREAKPOINT) {
                padding: 0 15px;
            }
        }
        &-title-wrapper {
            z-index: 2;
            //width: 228px; //TODO remove if have trouble
            background: white;
        }
        &-subtitle {
            margin: 0;
            letter-spacing: 0.6px;
            color: @warmGrayColor;
            font-weight: 100;
            text-transform: uppercase;
            text-align: center;
            z-index: 1;
        }
        &-divider {
            position: absolute;
            height: 1.1px;
            top: 23px;
            left: 0;
            background: @warmGrayColor;
            z-index: 0;
            right: 0;
            @media (min-width: @MOBILE_BREAKPOINT) {
                margin: 0 5px;
            }
        }
    }
    &__or {
        margin: 6px auto 16px auto;
        position: relative;
        width: 310px;
        text-align: center;
        &-wrapper {
            background-color: #fff;
            display: inline-block;
            padding: 0 18px;
            position: relative;
        }
        &:before {
            content: "";
            width: 100%;
            height: 1px;
            background-color: @warmGrayColor;
            position: absolute;
            top: 9px;
            left: 0;
        }
    }
    &__checkbox {
        &-container {
            margin-top: 26px;
            margin-bottom: 26px;
            width: 260px;
            @media (max-width: @MIN_DIALOG_LENGTH) {
                width: 270px;
            }
            &-row {
                display: flex;
                align-items: center;
                flex-direction: row;
                justify-content: flex-start;
            }
            &-row2 {
                display: flex;
                position: relative;
                margin-top: 15px;
            }
        }
    }
    &__textfield-wrapper {
        width: 310px;
        margin: 0 auto;
        @media (max-width: @MIN_DIALOG_LENGTH) {
            width: 270px;
        }
    }
    &__password {
        &-wrapper {
            position: relative;
            margin-bottom: 10px;
        }
    }
}


