.link {
    display: inline-block;
    font-size: 14px;
    line-height: 2.14;
    text-decoration: none;
    text-transform: uppercase;
    color: @colorMain;
    font-weight: @bold;
    transition: color .3s;
    &_center {
        width: 100%;
        text-align: center;
    }
    &:hover {
        color: @colorMainHover;
    }
    &:active {
        transition: none;
        color: @colorMainActive;
    }
}
