@vote-up: #f54646;
@vote-down: #70c570;
.vote {
    position: relative;
    cursor: pointer;
    &__input {
        display: none;
        position: absolute;
        left: 0;
        top: 0;
    }
    &__circle {
        display: inline-block;
        width: 75px;
        height: 75px;
        border-radius: 50%;
        border: solid 2px @colorBlack;
        transition: background .3s;
    }
    .icon {
        transition: fill .3s;
    }
    &_up {
        .vote__circle {
            border-color: @vote-up;
            .icon {
                fill: @vote-up;
            }
        }
        .vote__input {
            &:checked + .vote__circle {
                background: @vote-up;
                .icon {
                    fill: #fff;
                }
            }
        }
    }
    &_down {
        .vote__circle {
            border-color: @vote-down;
            .icon {
                fill: @vote-down;
            }
        }
        .vote__input {
            &:checked + .vote__circle {
                background: @vote-down;
                .icon {
                    fill: #fff;
                }
            }
        }
    }
}
