.reviews {
    margin-bottom: 36px;
    padding: 0 55px;
    box-sizing: border-box;
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    &__item {
        border-bottom: solid 1px @colorMainDisabled;
        padding-top: 12px;
        padding-bottom: 15px;
        &:last-child {
            border-bottom: none;
        }
        &:not(:first-child) {
            padding-top: 17px;
        }
    }
    &__more {
        text-align: center;
    }
}
