.post-effects {
    padding: 5px 0;
    &__list {
        list-style: none;
        margin: 0;
        padding: 0;
        text-align: center;
    }
    &__item {
        display: inline-block;
        margin-right: 10px;
        margin-bottom: 8px;
        font-size: 10px;
        font-weight: @bold;
        letter-spacing: 0.5px;
        width: 81px;
        height: 20px;
        line-height: 18px;
        border: solid 1px @colorBlack;
        box-sizing: border-box;
        .no-select();
        &:last-child {
            margin-right: 0;
        }
        &_energize {
            color: @energize;
            border-color: @energize;
        }
        &_chill {
            color: @chill;
            border-color: @chill;
        }
        &_sleep {
            color: @sleep;
            border-color: @sleep;
        }
        &_medicate {
            color: @medicate;
            border-color: @medicate;
        }
        &_create {
            color: @create;
            border-color: @create;
        }
        &_elevate {
            color: @elevate;
            border-color: @elevate;
        }
    }
}
