.review-voting {
    color: @colorBlack;
    text-align: center;
    .clearfix();
    &__header {
        margin-bottom: 8px;
    }
    &__title {
        font-size: 20px;
        font-weight: @normal;
        letter-spacing: 1px;
        margin: 0;
        text-transform: uppercase;
        .oswald();
    }
    &__product-name {
        font-size: 16px;
    }
    &__vendor {
        font-size: 16px;
    }
    &__question {
        float: left;
        width: 100%;
        margin-bottom: 5px;
    }
    &__question-title {
        font-size: 20px;
        font-weight: @normal;
        letter-spacing: 1px;
        text-transform: uppercase;
        margin: 0;
        margin-top: 15px;
        margin-bottom: 8px;
        .oswald();
    }
    &__answers {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    &__answers-item {
        display: inline-block;
        padding: 0 10px;
    }
    &_narrow {
        width: 310px;
        margin: 0 auto;
    }
}
