.share {
    color: @colorBlack;
    text-align: center;
    width: 270px;
    margin: 0 auto;
    .clearfix();
    &__header {
        margin-bottom: 8px;
    }
    &__title {
        font-size: 20px;
        font-weight: @normal;
        letter-spacing: 1px;
        margin: 0;
        text-transform: uppercase;
        .oswald();
    }
    &__text {
        font-size: 16px;
        margin: 0;
    }
    &__row {
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0;
        }
    }
}
