.tabs {
    .clearfix();
    &__inner {
        background: @colorMain;
    }
    &__container {
        margin-top: 15px;
    }
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        float: left;
        width: 100%;
    }
    &__item {
        float: left;
        width: 100% / 4;
        border-left: solid 1px #f34d85;
        box-sizing: border-box;
        &:last-child {
            border-right: solid 1px #f34d85;
        }
    }
    &__link {
        font-size: 14px;
        font-weight: @bold;
        color: #fff;
        float: left;
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center;
        .no-select();
        cursor: pointer;
        &_active {
            background: @colorMainActive !important;
        }
        transition: background .3s;
        &:hover {
            background: @colorMainHover;
        }
        &:active {
            transition: none;
            background: @colorMainActive;
        }
    }
}
