@import "vars";

.shipment {
    width: 100%;
    max-width: 310px;
    padding: 5px 0;
    &__row {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        &_left {
            justify-content: flex-start !important;
        }
    }

    &__address {
        flex: 1;
    }

    &__distance {
        width: 70px;
        margin-left: 20px;
    }

    &__icon {
        padding-top: 45px;
        margin-left: 9px;
    }
}

@media (min-width: @DESKTOP_BREAKPOINT) {
    .shipment {
        max-width: 310px;
    }
}
