.dispensary-short {
    margin-top: 20px;
    &__link {
        color: @colorMainBlue;
        transition: color .3s;
        text-decoration: none;
        font-weight: @bold;
        text-transform: uppercase;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
    &__text {
        margin: 0;
        font-size: 14px;
        color: @colorBlack;
        br {
            display: block;
        }
    }
}
