.btn-set {
    display: inline-block;
    margin-bottom: 27px;
    &__remark {
        text-align: center;
        color: @colorBlack;
        font-size: 12px;
        padding: 0 20px;
        margin: 0;
    }
    &__link {
        text-decoration: none;
        color: @colorMainBlue;
        font-weight: @bold;
        transition: color .3s;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
    .btn {
        margin-right: 18px;
        vertical-align: bottom;;
        &:last-child {
            margin-right: 0;
        }
    }
    &_center {
        width: 100%;
        text-align: center;
    }
    &_margin {
        margin-top: 57px;
        margin-bottom: 60px;
    }
    &_margin-top {
        margin-top: 7px;
    }
    &_margin-bottom {
        margin-bottom: 65px;
    }
}
