.dispensary-details {
    border: solid 1px @colorMainDisabled;
    background: @lightGrayColor;
    box-sizing: border-box;
    position: relative;
    text-align: center;
    padding-top: 33px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 18px;
     &:before {
        content: '';
        background: url('../assets/icons/medium-chevron-right.svg') no-repeat;
        display: inline-block;
        width: 10px;
        height: 18px;
        position: absolute;
        right: 13px;
        top: 50%;
        margin-top: -9px;
    }
    &__status {
        position: absolute;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        display: inline-block;
        background: #61bf61;
        font-size: 14px;
        color: #fff;
        height: 20px;
        line-height: 20px;
        padding: 0 10px;
        &_inactive {
            background: @colorMainDisabled;
        }
    }
    &__title {
        margin: 0;
        .oswald();
        font-size: 20px;
        font-weight: @normal;
        color: @colorBlack;
        text-transform: uppercase;
        margin-bottom: 5px;
    }
    &__place {
        font-size: 14px;
        color: @colorBlack;
        line-height: 1;
    }
    &__distance {
        font-size: 11px;
        color: @warmGrayColor;
        margin-bottom: 2px;
    }
    &__features {
        list-style: none;
        padding: 0;
        margin: 0;
        margin-bottom: 11px;
    }
    &__features-item {
        display: inline-block;
        margin-right: 21.5px;
        .icon {
            fill: @colorBlack;
        }
        &:last-child {
            margin-right: 0;
        }
    }
    &__features-text {
        font-size: 14px;
        color: @colorBlack;
        line-height: 1;
    }
    &__info {
        font-size: 11px;
        font-weight: @bold;
    }
}
