@import "vars";

.user-status {
    @linkFontSize: 14px;
    @linkMargin: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 5px;
    font-size: 0;
    &_padded {
        padding-bottom: @linkFontSize + @lightGrayColor;
    }
    &__label {
        display: inline-block;
        opacity: 0.9;
        &_rejected,
        &_not-applied {
            background-color: @colorMain;
        }
        &_applied,
        &_other {
            background-color: @colorOrange;
        }
        &_applied {
            background-color: @colorGreen;
        }
    }
    &__label-text {
        display: inline-block;
        font-family: @fontMain;
        font-size: 14px;
        color: white;
        padding: 1px 10px;
        text-transform: uppercase;
    }
    &__link {
        font-family: @fontMain;
        font-size: @linkFontSize;
        font-weight: 600;
        text-transform: uppercase;
        text-decoration: none;
        cursor: pointer;
        color: @colorMainBlue;
        margin-top: @linkMargin;
        letter-spacing: 0.8px;
        transition: color .3s;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            color: @colorMainBlueActive;
            transition: none;
        }
    }
}
