@import "vars";

.section-header {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 13px;
    width: 100%;
    max-width: 515px;
    position: relative;
    z-index: 1;
    br {
        display: inline-block;
    }
    &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 55%;
        width: 100%;
        border-bottom: solid 1px @warmGrayColor;
    }
    &__title {
        display: inline-block;
        font-family: @fontDecorative;
        margin: 0;
        padding-left: 16px;
        padding-right: 16px;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: 30px;
        font-weight: 500;
        line-height: 34px;
        text-align: center;
        background: #fff;
        position: relative;
        z-index: 5;
    }
    &__title-wrapper {
        z-index: 2;
        //width: 228px; //TODO remove if have trouble
        background: white;
    }
    &__subtitle {
        position: relative;
        margin: 0;
        margin-bottom: 4px;
        letter-spacing: 0.5px;
        color: @warmGrayColor;
        font-weight: 100;
        font-size: 16px;
        text-transform: uppercase;
        text-align: center;
        z-index: 1;
    }
    &__text {
        font-size: 14px;
        margin: 0;
        color: @warmGrayColor;
        text-align: center;
    }
    &__marked {
        display: inline-block;
        background: rgba(255,161,50,.9);
        color: #fff;
        height: 20px;
        line-height: 20px;
        font-size: 14px;
        padding: 0 12px;
    }
    &__divider {
        position: absolute;
        height: 1.1px;
        top: 33px;
        left: 0;
        background: @warmGrayColor;
        z-index: 0;
        margin: 0 10px 0 0;
        right: 0;
        display: none;
        &_wide {
            width: 512px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

@media (max-width: 512px) {
    .section-header {
        &__divider {
            width: 100%;
            &_wide {
                left: 50%;
            }
        }
    }
}
