@import "vars";

.user-questionaire {
    font-family: @fontMain;
    text-align: center;
    color: @colorBlack;
    &__legend {
        font-size: 16px;
        margin: 0 0 18px 0;
    }
    &__progress-wrapper {
        margin-bottom: 14px;
    }
    &__progress-label {
        text-align: center;
        font-size: 16px;
        font-weight: 700;
        margin: 0 0 5px 0;
    }
    &__progress-bar {
        & > div {
            background: @colorMain !important;
        }
    }
}
