.effects-selector {
    &__title {
        font-family: @fontDecorative;
        font-size: 24px;
        color: @colorBlack;
        text-transform: uppercase;
    }
    &__list {
        list-style: none;
        padding: 0;
        margin: -10px 0 0;
    }
    &__item {
        display: inline-block;
        cursor: pointer;
        width: 80px;
        margin: 10px 10px 0 0;
        padding: 0;
        font-family: @fontMain;
        font-size: 11px;
        font-weight: bold;
        line-height: 24px;
        text-align: center;
        text-overflow: ellipsis;
        text-transform: uppercase;
        color: white;
        border-radius: 12px;
        opacity: .3;
        transition: opacity .3s, background .3s, color .3s;
        background: @colorMainBlue;
        &_selected {
            opacity: 1;
        }
    }
    &_middle {
        .effects-selector__item {
            background: none;
            opacity: 1;
            border-radius: 15px;
            border: solid 2px #ccc;
            line-height: 27px;
            width: 95px;
            height: 29px;
            margin-right: 2px;
            font-size: 13px;
            &:nth-child(3n) {
                margin-right: 0;
            }
            &_energize {
                border-color: @energize;
                color: @energize;
                &.effects-selector__item_active {
                    color: #fff;
                    background: @energize;
                }
            }
            &_chill {
                border-color: @chill;
                color: @chill;
                &.effects-selector__item_active {
                    color: #fff;
                    background: @chill;
                }
            }
            &_sleep {
                border-color: @sleep;
                color: @sleep;
                &.effects-selector__item_active {
                    color: #fff;
                    background: @sleep;
                }
            }
            &_medicate {
                border-color: @medicate;
                color: @medicate;
                &.effects-selector__item_active {
                    color: #fff;
                    background: @medicate;
                }
            }
            &_create {
                border-color: @create;
                color: @create;
                &.effects-selector__item_active {
                    color: #fff;
                    background: @create;
                }
            }
            &_elevate {
                border-color: @elevate;
                color: @elevate;
                &.effects-selector__item_active {
                    color: #fff;
                    background: @elevate;
                }
            }
            &_margin-bottom {
                margin-bottom: 10px !important;
            }
        }
    }
}
