.rating {
    .clearfix();
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        float: left;
    }
    &__item {
        float: left;
        margin-right: 2px;
        .icon {
            fill: @colorBlack;
            float: left;
        }
        &:last-child {
            margin-right: 0;
        }
    }
    &__count {
        float: left;
        font-size: 11px;
        font-weight: @bold;
        color: @colorMainBlue;
        transition: color .3s;
        text-decoration: none;
        margin-left: 5px;
        line-height: 13px;
        height: 13px;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
    &_right {
        float: right;
    }
}
