.icons-grid {
    &__list {
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
    }
    &__item {
        width: 100px;
        height: 100px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-item: center;
        border: solid 1px #ccc;
        padding-top: 10px;
        box-sizing: border-box;
        margin-right: 10px;
        margin-bottom: 10px;
    }
    &__title {
        font-size: 12px;
        font-style: italic;
        color: @colorBlack;
        display: inline-block;
        width: 100%;
        text-align: center;
    }
}
