.progress {
    color: @colorBlack;
    margin-top: 25px;
    margin-bottom: 34px;
    &__title {
        font-size: 14px;
        font-weight: @bold;
        text-transform: uppercase;
        margin: 0;
        margin-bottom: 8px;
        text-align: center;
        line-height: 1;
        .clearfix();
    }
    &__body {
        float: left;
        width: 100%;
        height: 6px;
        background: @colorMainDisabled;
        position: relative;
    }
    &__bar {
        background: @colorBlack;
        position: absolute;
        left: 0;
        top: 0;
        width: 40%;
        height: 6px;
    }
    &__remark {
        display: inline-block;
        width: 100%;
        font-size: 14px;
        color: @warmGrayColor;
        text-align: right;
        margin-top: 5px;
        line-height: 1;
    }
}
