.photos {
    max-width: 740px;
    margin: -8px auto 0;
    margin-bottom: 60px;
    .clearfix();
    &__item {
        width: 230px;
        height: 223px;
        overflow: hidden;
        float: left;
        margin: 8px;
        cursor: pointer;
        position: relative;
        box-shadow: 0px 3px 10.5px 0 rgba(0, 0, 0, 0.2);
        background: url('http://placekitten.com/500/500') no-repeat center;
        background-size: cover;
        &:after {
            content: '';
            background: @colorMainBlue;
            transition: opacity .3s;
            opacity: 0;
            position: absolute;
            width: 100%;
            left: 0;
            top: 0;
            height: 100%;
        }
        &:before {
            content: 'VIEW';
            font-size: 20px;
            opacity: 0;
            transition: opacity .3s;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateY(-50%) translateX(-50%);
            text-transform: uppercase;
            color: white;
            z-index: 10;
            .oswald();
        }
        &:hover {
            &:after {
                opacity: .6;
            }
            &:before {
                opacity: 1;
            }
        }
    }
}
