@import "vars";

.pac-container {
    top: 410px !important;
}

.address-set-screen {
    .pac-container {
        top: 288px !important;
    }
}

.shop-screen-tablet {
    .pac-container {
        top: 190px !important;
    }
}

.shop-screen-desktop {
    .pac-container {
        top: 260px !important;
    }
}

.pac-logo:after {
    display: none !important;
}

.pac-item {
    font-family: @fontMain;
    font-size: 16px;
    cursor: pointer;
    padding-left: 10px;
}

.pac-item:hover {
    color: #40bccf;
}

.pac-item-query {
    font-size: 16px;
}

.pac-item-query:hover {
    color: #40bccf;
}

.pac-icon-marker {
    display: none !important;
}

#homeAddressAutocomplete {
    text-overflow: ellipsis;
    white-space: nowrap;
}

#homeAddressAutocomplete::-webkit-input-placeholder {
    color: transparent;
}

#homeAddressAutocomplete:-moz-placeholder { /* Firefox 18- */
    color: transparent;
}

#homeAddressAutocomplete::-moz-placeholder { /* Firefox 19+ */
    color: transparent;
}

#homeAddressAutocomplete:-ms-input-placeholder {
    color: transparent;
}

[id^="address"] {
    &::-webkit-input-placeholder {
        color: transparent;
    }

    &:-moz-placeholder { /* Firefox 18- */
        color: transparent;
    }

    &::-moz-placeholder { /* Firefox 19+ */
        color: transparent;
    }

    &:-ms-input-placeholder {
        color: transparent;
    }
}
