.pager {
    margin-top: 30px;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .no-select();
    &__content {
        font-family: @fontDecorative;
        font-size: 27px;
        font-weight: normal;
        line-height: 27px;
        color: @colorMainBlue;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    &__item {
        margin-right: 18px;
        cursor: pointer;
        transition: color .3s;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            color: @colorMainBlueActive;
            transition: none;
        }
        &:last-child {
            margin-right: 0;
        }
        &_selected {
            color: @colorBlack;
            cursor: default;
            &:hover {
                color: @colorMain;
            }
            &:active {
                color: @colorMain;
            }
        }
    }
    &__text {
        font-family: @fontMain;
        font-size: 16px;
        margin-top: 20px;
        &-current {
            font-weight: @bold;
        }
    }
}
