.product-details {
    padding-left: 32px;
    padding-right: 48px;
    &__header {
        .clearfix();
        margin-bottom: 8px;
    }
    &__vendor {
        font-size: 11px;
        text-transform: uppercase;
        color: @colorBlack;
        float: left;
    }
    &__link {
        text-decoration: none;
        color: @colorMainBlue;
        font-weight: @bold;
        transition: color .3s;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
    &__composition {
        font-size: 11px;
        color: @colorBlack;
        text-align: center;
    }
    &__composition-item {
        display: inline-block;
        margin-right: 15px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__description {
        margin-top: 41.5px;
        padding: 0 55px;
        display: inline-block;
        width: 100%;
        box-sizing: border-box;
    }
    &__description-title {
        .oswald();
        font-size: 30px;
        font-weight: @normal;
        color: @colorBlack;
        text-align: center;
        line-height: 1;
        letter-spacing: 1.5px;
        text-transform: uppercase;
        margin: 0;
    }
    &__description-text {
        font-size: 16px;
        line-height: 1.3em;
        margin: 0;
        margin-top: 7px;
        margin-bottom: 35px;
        color: @colorBlack;
    }
    &__description-read-more {
        color: @colorMainBlue;
        font-weight: @bold;
        font-size: 16px;
        position: relative;
        cursor: pointer;
        transition: color .3s;
        padding-left: 5px;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
}
