@import "vars";

/* hide spinners for text fields in Chrome */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* hide spinners for text fields in Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

div br {
    display: none;
}

body {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
}

.overflowed {
    overflow: hidden !important;
    max-height: 0 !important;
    min-height: 0 !important;
}

header {
    transition: background .5s ease;
}

.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.hidden {
    display: none;
}

main {
    /* clearfix */
    overflow: auto;
}

button[disabled] {
    opacity: .8;
}

input[type=text]::-ms-clear {
    display: none !important;
    opacity: 0 !important;
    visibility: hidden !important;
}

.mobile-show {
    display: none;
}
.mobile-hide {
    display: block;
}
