@import "vars";

.account-rating {
    color: @colorBlack;
    &__count {
        font-family: @fontDecorative;
        font-size: 30px;
        letter-spacing: 1.5px;
        text-align: center;
        margin: 0;
    }
    &__label {
        font-family: @fontMain;
        font-size: 11px;
        font-weight: bold;
        letter-spacing: 0.6px;
        text-align: center;
        text-transform: uppercase;
        margin: 0;
    }
    &__points {
        font-family: @fontMain;
        font-size: 11px;
        letter-spacing: 0.6px;
        text-align: center;
        margin: 0;
    }
}
