.weight {
    float: left;
    text-align: center;
    width: 100%;
    cursor: pointer;
    transition: opacity .3s;
    .clearfix();
    &:hover {
        opacity: .9;
    }
    .icon {
        fill: #fff;
        float: left;
        width: 100%;
    }
    &__input {
        display: none;
        &:checked + .weight__container {
            background: @colorMain;
            .weight__text {
                color: #fff;
            }
        }
    }
    &__container {
        padding-top: 8px;
        padding-bottom: 5px;
        background: @colorBlack;
        float: left;
        width: 100%;
        height: 55px;
        transition: background .3s;
        box-sizing: border-box;
    }
    &__text {
        float: left;
        width: 100%;
        font-size: 14px;
        color: @warmGrayColor;
        margin-bottom: 4px;
        line-height: 1;
        transition: color .3s;
    }
    &__price {
        float: left;
        font-size: 14px;
        margin-bottom: 2px;
        color: #fff;
        line-height: 1;
        width: 100%;
    }
}
