.review {
    &__header {
        margin-bottom: 10px;
        .clearfix();
    }
    &__name {
        .oswald();
        font-size: 16px;
        text-transform: uppercase;
        color: @colorBlack;
        float: left;
    }
    &__text {
        margin: 0;
        font-size: 16px;
        color: @colorBlack;
        line-height: 1.3em;
    }
}
