.rating-detailed {
    text-align: center;
    margin-top: 20px;
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        display: inline-block;
        .clearfix();
    }
    &__item {
        float: left;
        margin-right: 4px;
        .icon {
            fill: @colorBlack;
        }
        &:last-child {
            margin-right: 0;
        }
    }
    &__info {
        text-align: center;
        list-style: none;
        padding: 0;
        margin: 0 auto;
        margin-top: 5px;
        width: 320px;
        .clearfix();
    }
    &__info-item {
        float: left;
        width: 100% / 3;
        &:last-child {
            margin-right: 0;
        }
    }
    &__info-count {
        font-size: 16px;
        font-weight: @bold;
        color: @colorBlack;
        line-height: 1;
    }
    &__info-text {
        font-size: 14px;
        color: @colorBlack;
    }
}
