.order-details {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-top: 20px;
    &__item {
        color: @colorBlack;
        margin-bottom: 14px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__key {
        font-size: 20px;
        text-transform: uppercase;
        font-weight: @bold;
    }
    &__value {
        font-size: 16px;
        line-height: 21px;
        br {
            display: block;
        }
    }
}
