.cart {
    coor: @colorBlack;
    padding-top: 15px;
    &__header {
        text-align: center;
        padding-top: 8px;
    }
    &__remark {
        font-size: 11px;
    }
    &__dispensary {
        font-size: 18px;
        text-transform: uppercase;
        font-weight: @bold;
    }
    &__status {
        display: inline-block;
        background: #61bf61;
        font-size: 14px;
        color: #fff;
        height: 20px;
        line-height: 20px;
        padding: 0 10px;
        margin-top: 5px;
        margin-bottom: 25px;
        &_inactive {
            background: @colorMainDisabled;
        }
    }
    &__row {
        .clearfix();
    }
    &__column {
        float: left;
        width: 50%;
        text-align: center;
    }
    &__key {
        font-size: 14px;
        line-height: 1;
    }
    &__value {
        font-size: 14px;
        color: @colorMain;
    }
    &__link {
        font-size: 14px;
        font-weight: @bold;
        color: @colorMainBlue;
        transition: color .3s;
        text-transform: uppercase;
        text-decoration: none;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
}

