.effects {
    padding: 5px 0;
    padding-bottom: 2px;
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        text-align: center;
    }
    &__item {
        display: inline-block;
        font-size: 11px;
        font-weight: @bold;
        letter-spacing: 0.6px;
        line-height: 24.5px;
        width: 81px;
        height: 24.5px;
        color: #fff;
        text-transform: uppercase;
        border-radius: 12.3px;
        margin-right: 10px;
        margin-bottom: 8px;
        box-sizing: border-box;
        .no-select();
        &:last-child {
            margin-right: 0;
        }
        &_energize {
            background: @energize;
        }
        &_chill {
            background: @chill;
        }
        &_sleep {
            background: @sleep;
        }
        &_medicate {
            background: @medicate;
        }
        &_create {
            background: @create;
        }
        &_elevate {
            background: @elevate;
        }
        &_margin-bottom {
            margin-bottom: 10px !important;
        }
    }
    &_big {
        .effects__item {
            line-height: 32px;
            width: 106px;
            height: 32px;
            font-size: 14px;
            border-radius: 16px;
            letter-spacing: 0.7px;
        }
    }
}
