.socials {
    list-style: none;
    padding: 0;
    margin: 0;
    display: inline-block;
    .clearfix();
    &__item {
        float: left;
        margin-right: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        text-decoration: none;
        float: left;
        transition: opacity .3s;
        .icon {
            float: left;
        }
        &:hover {
            opacity: .7;
        }
        &:active {
            transition: none;
            opacity: 1;
        }
    }
}
