.icon {
    display: inline-block;
    width: 32px;
    height: 32px;
}
.icon-share {
    width: 40px;
    height: 35px;
}
.icon-medical {
    width: 23px;
    height: 23px;
}
.icon-credit-card {
    width: 23px;
    height: 17px;
}
.icon-delivery {
    width: 25px;
    height: 22.5px;
}
.icon-tiny-plus {
    width: 9px;
    height: 9px;
}
.icon-tiny-tick {
    width: 9px;
    height: 7px;
}
.icon-heart-o {
    width: 36.5px;
    height: 32.5px;
}
.icon-small-star-o {
    width: 13px;
    height: 13px;
}
.icon-small-star {
    width: 13px;
    height: 13px;
}
.icon-big-star-o {
    width: 36px;
    height: 36px;
}
.icon-big-star {
    width: 36px;
    height: 36px;
}
.icon-medium-star-o {
    width: 19px;
    height: 19px;
}
.icon-medium-star {
    width: 19px;
    height: 19px;
}
.icon-small-chevron-right {
    width: 7px;
    height: 8px;
}
.icon-plus {
    width: 20px;
    height: 20px;
}
.icon-minus {
    width: 20px;
    height: 2px;
}
.icon-small-plus {
    width: 13px;
    height: 13px;
}
.icon-pick-up {
    width: 15.25px;
    height: 15.75px;
}
.icon-thumb-up {
    width: 77.25px;
    height: 77.25px;
}
.icon-thumb-down {
    width: 77.25px;
    height: 77.25px;
    transform: rotate(180deg);
}
.icon-chevron-left {
    width: 36.3px;
    height: 70px;
    fill: none;
    stroke: currentColor;
    transform: rotateY(180deg);
}
.icon-chevron-right {
    width: 36.3px;
    height: 70px;
    fill: none;
    stroke: currentColor;
}
.icon-facebook {
    width: 50px;
    height: 50px;
}
.icon-gplus {
    width: 50px;
    height: 50px;
}
.icon-instagram {
    width: 51.5px;
    height: 51.5px;
}
.icon-twitter {
    width: 50px;
    height: 50px;
}




.icon-edit {
    width: 20px;
    height: 20px;
}


.icon-left-arrow,
.icon-right-arrow {
    width: 10px;
    height: 18px;
}

.icon-arrow-down,
.icon-arrow-up {
    width: 18px;
    height: 10px;
    transition: transform .5s ease;
    &_rotate {
        transform: rotate(-180deg);
    }
    &_rotate_90 {
        transform: rotate(-90deg);
    }
}
