@import "vars";

.btn {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    background: @colorMain;
    font-size: 20px;
    color: #fff;
    .oswald();
    text-transform: uppercase;
    text-align: center;
    border: none;
    letter-spacing: 0.05em;
    vertical-align: middle;
    transition: background .3s, color .3s;
    padding: 0 10px;
    text-decoration: none;
    box-sizing: border-box;
    .no-select();
    cursor: pointer;
    &:hover {
        background: @colorMainHover;
    }
    &:active {
        transition: none;
        background: @colorMainActive;
    }
    &:focus {
        outline: none;
    }
    &_secondary {
        background: #fff;
        border: solid 2px @colorMain;
        color: @colorMain;
        height: 30px;
        line-height: 26px;
        letter-spacing: 0.7px;
        font-size: 14px;
        font-weight: @bold;
        cursor: pointer;
        .open-sans();
        &:hover {
            background: #fff;
            color: @colorMainHover;
            border-color: @colorMainHover;
        }
        &:active {
            transition: none;
            background: #fff;
            color: @colorMainActive;
            border-color: @colorMainActive;
        }
    }
    &_wide {
        width: 100%;
    }
    &_140 {
        width: 140px;
    }
    &_transparent {
        background: none;
        border: none;
        line-height: 1;
        font-size: 16px;
        font-weight: @bold;
        .open-sans();
        cursor: pointer;
        color: @colorMainBlue;
        .icon {
            fill: @colorMainBlue;
            transition: fill .3s;
        }
        &:hover {
            background: none;
            color: @colorMainBlueHover;
            .icon {
                fill: @colorMainBlueHover;
            }
        }
        &:active {
            transition: none;
            background: none;
            color: @colorMainBlueActive;
            .icon {
                fill: @colorMainBlueActive;
            }
        }
    }
    &_icon {
        background: none;
        border: none;
        height: auto;
        line-height: 1;
        cursor: pointer;
        margin-bottom: -1px;
        .icon {
            fill: @colorMainBlue;
            transition: fill .3s;
        }
        &:hover {
            background: none;
            .icon {
                fill: @colorMainBlueHover;
            }
        }
        &:active {
            transition: none;
            background: none;
            .icon {
                fill: @colorMainBlueActive;
            }
        }
    }
    &_pink {
        border: 2px solid @colorMain;
    }
    &_mui-flat-pink {
        & > div {
            //    material hack
            display: inline;
            padding: 7px 0;
            border: 2px solid @colorMain;
            color: @colorMain;
        }
    }
    &_margin-top {
        margin-top: 11px;
    }
     &_margin-bottom {
        margin-bottom: 11px;
    }
}

