.total {
    color: @colorBlack;
    margin-top: 13px;
    &__list {
        list-style: none;
        padding: 0;
        padding-bottom: 11px;
        margin: 0;
        border-bottom: solid 1px @colorMainDisabled;
    }
    &__item {
        .clearfix();
        margin-bottom: 2px;
    }
    &__key {
        font-size: 14px;
        float: left;
    }
    &__value {
        font-size: 14px;
        float: right;
    }
    &__grand {
        margin-top: 18px;
        .clearfix();
    }
    &__grand-key {
        .oswald();
        font-size: 16px;
        float: left;
        text-transform: uppercase;
    }
    &__grand-value {
        font-size: 16px;
        float: right;
    }
}
