.weights {
    width: 100%;
    margin-top: 14px;
    margin-bottom: 10px;
    .clearfix();
    &__list {
        list-style: none;
        padding: 0;
        margin: 0;
        float: left;
        width: 100%;
    }
    &__item {
        float: left;
        width: 100% / 5;
        border-right: solid 1px #fff;
        box-sizing: border-box;
        &:last-child {
            border-right: none;
        }
    }
}
