.order {
    padding-top: 10px;
    padding-bottom: 20px;
    color: @colorBlack;
    .clearfix();
    &__img {
        display: inline-block;
        width: 50px;
        height: 50px;
        float: left;
        background-image: url('http://lorempixel.com/50/50/nature');
        background-size: cover;
        box-shadow: 0px 3px 5px 0px rgba(0,0,0,.3);
    }
    &__container {
        float: left;
        width: ~'calc(100% - 100px)';
        padding-left: 14px;
        box-sizing: border-box;
    }
    &__price {
        font-size: 16px;
        font-weight: @bold;
        line-height: 1;
    }
    &__name {
        line-height: 19px;
    }
    &__quantity {
        display: inline-block;
        width: 50px;
        height: 50px;
        line-height: 50px;
        float: left;
        .oswald();
        font-size: 16px;
        text-align: center;
        .incrementer {
            margin-top: -9px;
            margin-left: 8px;
        }
        &_incrementer {
            height: auto;
            line-height: 1;
        }
    }
    &__footer {
        display: inline-block;
        width: 100%;
        text-align: center;
    }
    &__remove-btn {
        display: inline-block;
        text-align: center;
        font-size: 14px;
        font-weight: @bold;
        text-transform: uppercase;
        text-decoration: none;
        color: @colorMainBlue;
        transition: color .3s;
        padding: 10px 0;
        &:hover {
            color: @colorMainBlueHover;
        }
        &:active {
            transition: none;
            color: @colorMainBlueActive;
        }
    }
    &_checkout {
        padding-bottom: 0;
    }
}
